d{background-color:#06f;color:#fff}.vue-drp-presets{background-color:#f5f5f5;box-sizing:border-box;padding:1em;width:21%}.vue-drp-preset{cursor:pointer;margin:.2em 0;padding:.3em}.vue-drp-preset:hover{background-color:#fafafa}.vue-drp-selected-preset{background-color:#fcfcfc;border:1px solid #06f;color:#06f;font-weight:500}.vue-drp-apply{background-color:#06f;color:#fff;cursor:pointer;font-weight:500;margin-top:1em;padding:.4em;text-align:center}.vue-drp-apply:hover{opacity:.8}.vue-drp-apply[disabled]{cursor:not-allowed;opacity:.5}.vue-drp-clear{cursor:pointer;font-weight:300;margin-top:.5em;padding:.2em .2em 0;text-align:center}", "",{"version":3,"sources":["webpack://./resources/mixes/app/js/Shared/DatePicker.vue"],"names":[],"mappings":"AACA,uBACE,UAAW,CACX,UACF,CAEA,eAEE,kBAAmB,CACnB,cAAe,CAFf,YAAa,CAIb,cAAe,CACf,eAAgB,CAFhB,wBAAiB,CAAjB,qBAAiB,CAAjB,gBAGF,CAEA,sBAGE,iCAAkC,CAClC,kCAAmC,CAHnC,eAAgB,CAKhB,cAAe,CAJf,gCAAkC,CASlC,iBAAkB,CAJlB,iBAAkB,CAClB,mBAAoB,CAHpB,eAAgB,CAKhB,aAAc,CAEd,sBAAuB,CAHvB,mBAIF,CAEA,kBAME,eAAiB,CADjB,kCAAmC,CADnC,YAAa,CAGb,YAAa,CAJb,eAAgB,CAMhB,SAAU,CARV,iBAAkB,CAClB,OAAQ,CAMR,WAAY,CAEZ,SACF,CAEA,eAIE,qBAAsB,CADtB,UAAW,CAFX,cAAe,CAQf,kBAAmB,CADnB,iBAAoB,CAHpB,iBAAkB,CAClB,OAAQ,CACR,KAAM,CALN,wBAAiB,CAAjB,qBAAiB,CAAjB,gBAQF,CAEA,qBACE,eACF,CAEA,qBACE,UACF,CAEA,qBACE,MACF,CAEA,eAGE,aAAc,CAFd,YAAc,CACd,eAEF,CAEA,gBACE,YAAa,CACb,MAAO,CACP,kBAAmB,CAEnB,aAAc,CADd,iBAEF,CAEA,eACE,MAAO,CACP,aACF,CAEA,qBAIE,UAAW,CAFX,eAAiB,CACjB,cAAgB,CAFhB,iBAIF,CAEA,4BACE,cAAe,CAEf,eAAgB,CAChB,gBAAkB,CAFlB,wBAAiB,CAAjB,qBAAiB,CAAjB,gBAGF,CAEA,wCACE,UACF,CAEA,cACE,UAAW,CACX,iBACF,CAEA,oBACE,eACF,CAEA,cACE,WAAY,CACZ,kBACF,CAEA,oBACE,eACF,CAEA,kBAEE,aAAc,CADd,YAAa,CAGb,qBAAsB,CADtB,mBAEF,CAEA,sBACE,MACF,CAEA,cACE,YACF,CAEA,aACE,WAAY,CAGZ,cAAgB,CADhB,wBAAiB,CAAjB,qBAAiB,CAAjB,gBAAiB,CADjB,aAGF,CAEA,oBACE,cACF,CAEA,0BACE,kCAAuC,CACvC,UACF,CAEA,kBACE,qBAAsB,CACtB,UACF,CAEA,iBAEE,wBAAyB,CADzB,qBAAsB,CAGtB,WAAY,CADZ,SAEF,CAEA,gBACE,cAAe,CACf,aAAe,CACf,YACF,CAEA,sBACE,wBACF,CAEA,yBAGE,wBAAyB,CAFzB,qBAAsB,CACtB,UAAW,CAEX,eACF,CAEA,eAGE,qBAAsB,CACtB,UAAW,CAFX,cAAe,CAKf,eAAgB,CANhB,cAAe,CAKf,YAAc,CADd,iBAGF,CAEA,qBACE,UACF,CAEA,yBAEE,kBAAmB,CADnB,UAEF,CAEA,eAEE,cAAe,CAGf,eAAgB,CAJhB,eAAgB,CAGhB,mBAAoB,CADpB,iBAGF","sourcesContent":["<style>\n.vue-date-range-picker {\n  color: #333;\n  width: 100%;\n}\n\n.vue-drp-input {\n  display: flex;\n  align-items: center;\n  cursor: pointer;\n  user-select: none;\n  font-size: .8em;\n  font-weight: 300;\n}\n\n.vue-drp-input:before {\n  content: \"\\f073\";\n  font-family: 'Font Awesome 5 Free';\n  -moz-osx-font-smoothing: grayscale;\n  -webkit-font-smoothing: antialiased;\n  font-weight: 900;\n  display: inline;\n  font-style: normal;\n  font-variant: normal;\n  text-rendering: auto;\n  line-height: 1;\n  font-size: smaller;\n  margin-inline-end: .5em;\n}\n\n.vue-drp-calendar {\n  position: absolute;\n  right: 0;\n  margin-top: -1px;\n  display: flex;\n  box-shadow: -3px 4px 12px -1px #ccc;\n  background: white;\n  height: 270px;\n  width: 700px;\n  padding: 0;\n  z-index: 9;\n}\n\n.vue-drp-close {\n  cursor: pointer;\n  user-select: none;\n  color: #fff;\n  background-color: #06f;\n  position: absolute;\n  right: 0;\n  top: 0;\n  padding: 0.4em 0.7em;\n  font-weight: bolder;\n}\n\n.vue-drp-close:after {\n  content: '\\2715';\n}\n\n.vue-drp-close:hover {\n  opacity: 0.8;\n}\n\n.vue-drp-months-view {\n  flex: 1;\n}\n\n.vue-drp-title {\n  padding: 0.5em;\n  text-align: left;\n  font-size: 2em;\n}\n\n.vue-drp-months {\n  display: flex;\n  flex: 1;\n  flex-flow: row wrap;\n  text-align: center;\n  padding: 0 1em;\n}\n\n.vue-drp-month {\n  flex: 1;\n  padding: 0 1em;\n}\n\n.vue-drp-month-title {\n  position: relative;\n  font-weight: bold;\n  padding: 0.5em 0;\n  color: #06f;\n}\n\n.vue-drp-prev, .vue-drp-next {\n  cursor: pointer;\n  user-select: none;\n  font-size: 2.5em;\n  line-height: 0.4em;\n}\n\n.vue-drp-prev:hover, .vue-drp-next:hover {\n  opacity: 0.8;\n}\n\n.vue-drp-prev {\n  float: left;\n  margin-left: -0.5em;\n}\n\n.vue-drp-prev:after {\n  content: '\\2190';\n}\n\n.vue-drp-next {\n  float: right;\n  margin-right: -0.5em;\n}\n\n.vue-drp-next:after {\n  content: '\\2192';\n}\n\n.vue-drp-weekdays {\n  display: flex;\n  color: #b3b3b3;\n  padding-bottom: 0.5em;\n  font-family: monospace;\n}\n\n.vue-drp-weekdays > div {\n  flex: 1;\n}\n\n.vue-drp-week {\n  display: flex;\n}\n\n.vue-drp-day {\n  flex-grow: 1;\n  width: 14.285%;\n  user-select: none;\n  font-size: 0.9em;\n}\n\n.vue-drp-selectable {\n  cursor: pointer;\n}\n\n.vue-drp-selectable:hover {\n  background-color: rgba(51, 51, 51, 0.1);\n  color: #333;\n}\n\n.vue-drp-selected {\n  background-color: #06f;\n  color: #fff;\n}\n\n.vue-drp-presets {\n  box-sizing: border-box;\n  background-color: #f5f5f5;\n  width: 21%;\n  padding: 1em;\n}\n\n.vue-drp-preset {\n  cursor: pointer;\n  margin: 0.2em 0;\n  padding: 0.3em;\n}\n\n.vue-drp-preset:hover {\n  background-color: #fafafa;\n}\n\n.vue-drp-selected-preset {\n  border: 1px solid #06f;\n  color: #06f;\n  background-color: #fcfcfc;\n  font-weight: 500;\n}\n\n.vue-drp-apply {\n  margin-top: 1em;\n  cursor: pointer;\n  background-color: #06f;\n  color: #fff;\n  text-align: center;\n  padding: 0.4em;\n  font-weight: 500;\n}\n\n.vue-drp-apply:hover {\n  opacity: 0.8;\n}\n\n.vue-drp-apply[disabled] {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.vue-drp-clear {\n  margin-top: .5em;\n  cursor: pointer;\n  text-align: center;\n  padding: .2em .2em 0;\n  font-weight: 300;\n}\n</style>\n\n<template>\n  <div class=\"vue-date-range-picker\">\n    <div class=\"vue-drp-input\" :class=\"{'chosen': dateRangeStart && dateRangeEnd}\"\n         @click=\"toggleCalendar\" v-text=\"rangeString\"/>\n\n    <div class=\"vue-drp-calendar\" v-if=\"open\">\n\n      <div class=\"vue-drp-close\" @click=\"toggleCalendar\"></div>\n\n      <div class=\"vue-drp-months-view\">\n\n        <div class=\"vue-drp-title\">\n          Choose Dates\n        </div>\n\n        <div class=\"vue-drp-months\">\n          <div class=\"vue-drp-month\">\n            <div class=\"vue-drp-month-title\">\n              <div class=\"vue-drp-prev\" @click=\"goPrevMonth\"></div>\n              <span>\n                            {{ locale.monthes[active.start.month] }}\n                            {{ active.start.year }}\n                        </span>\n            </div>\n\n            <div class=\"vue-drp-weekdays\">\n              <div v-for=\"wdi in 7\" v-text=\"locale.weekdays[wdi-1]\"></div>\n            </div>\n\n            <div class=\"vue-drp-week\" v-for=\"wi in 6\">\n              <div class=\"vue-drp-day\" v-for=\"di in 7\"\n                   :class=\"{\n                             'vue-drp-selectable': isSelectable(wi, di, startMonthDay, endMonthDate),\n                             'vue-drp-selected': isInRange(wi, di),\n                             }\"\n                   @click=\"select(wi, di)\"\n                   v-text=\"getDayCell(wi, di, startMonthDay, endMonthDate)\"></div>\n            </div>\n          </div>\n\n          <div class=\"vue-drp-month\">\n            <div class=\"vue-drp-month-title\">\n                        <span>\n                            {{ locale.monthes[nextActiveMonth] }}\n                            {{ active.end.year }}\n                        </span>\n              <div class=\"vue-drp-next\" @click=\"goNextMonth\"></div>\n            </div>\n\n            <div class=\"vue-drp-weekdays\">\n              <div v-for=\"wdi in 7\" v-text=\"locale.weekdays[wdi-1]\"></div>\n            </div>\n\n            <div class=\"vue-drp-week\" v-for=\"wi in 6\">\n              <div class=\"vue-drp-day\" v-for=\"di in 7\"\n                   :class=\"{\n                             'vue-drp-selectable': isSelectable(wi, di, startNextMonthDay, endNextMonthDate),\n                             'vue-drp-selected': isInRange(wi, di, true),\n                             }\"\n                   @click=\"select(wi, di, true)\"\n                   v-text=\"getDayCell(wi, di, startNextMonthDay, endNextMonthDate)\"></div>\n            </div>\n          </div>\n        </div>\n      </div>\n\n      <div class=\"vue-drp-presets\">\n        <div class=\"vue-drp-preset\" v-for=\"preset in presets\"\n             :class=\"{'vue-drp-selected-preset': (selectedPreset === preset.label)}\"\n             v-text=\"preset.label\" @click=\"setPreset(preset)\"></div>\n\n        <div class=\"vue-drp-apply\" tabindex=\"1\" role=\"button\" :disabled=\"dateRangeEnd === null ? true : null\"\n             @click=\"apply\">Apply\n        </div>\n\n        <div class=\"vue-drp-clear\" tabindex=\"1\" role=\"button\" :disabled=\"!dateRangeEnd ? true : undefined\"\n             @click=\"clear\">Clear\n        </div>\n      </div>\n\n    </div>\n  </div>\n</template>\n\n<script>\nimport dayjs from 'dayjs';\n\nexport default {\n  name: 'DatePicker',\n  props: {\n    format: {\n      type: String,\n      required: false,\n      default: 'DD MMM YYYY',\n    },\n\n    presets: {\n      type: Array,\n      required: false,\n      default() {\n        return [\n          {\n            label: 'Today',\n            dateRange: {\n              start: dayjs(),\n              end: dayjs(),\n            },\n          },\n          {\n            label: 'This Month',\n            dateRange: {\n              start: dayjs().startOf('month'),\n              end: dayjs(),\n            },\n          },\n          {\n            label: 'Last Month',\n            dateRange: {\n              start: dayjs().subtract(1, 'month').startOf('month'),\n              end: dayjs().subtract(1, 'month').endOf('month'),\n            },\n          },\n          {\n            label: 'Last 30 days',\n            dateRange: {\n              start: dayjs().subtract(30, 'day'),\n              end: dayjs(),\n            },\n          },\n        ];\n      },\n    },\n\n    value: {type: Array},\n  },\n\n  data() {\n    return {\n      locale: {\n        monthes: [\n          'January',\n          'February',\n          'March',\n          'April',\n          'May',\n          'June',\n          'July',\n          'August',\n          'September',\n          'October',\n          'November',\n          'December',\n        ],\n        weekdays: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],\n      },\n\n      open: false,\n\n      selectedPreset: '',\n\n      active: {\n        start: {\n          month: new Date().getMonth(),\n          year: new Date().getFullYear(),\n        },\n        end: {\n          year: new Date().getFullYear(),\n        },\n      },\n\n      isFirstChoice: true,\n\n      dateRangeStartRaw: null,\n      dateRangeEndRaw: null,\n\n    };\n  },\n\n  computed: {\n\n    /**\n     * Assure date is at the beginning of the selected day\n     */\n    dateRangeStart: {\n      get() {\n        return this.dateRangeStartRaw;\n      },\n\n      set(date) {\n        if (date && date.startOf) {\n          this.dateRangeStartRaw = date.startOf('day');\n        } else {\n          this.dateRangeStartRaw = date;\n        }\n      },\n    },\n\n    /**\n     * Assure date is at the end of the selected day\n     */\n    dateRangeEnd: {\n      get() {\n        return this.dateRangeEndRaw;\n      },\n\n      set(date) {\n        if (date && date.endOf) {\n          this.dateRangeEndRaw = date.endOf('day');\n        } else {\n          this.dateRangeEndRaw = date;\n        }\n      },\n    },\n\n    /**\n     * Get date range string\n     *\n     * @return {string}\n     */\n    rangeString() {\n      let string = '';\n\n      if (this.dateRangeStart && this.dateRangeStart.format) {\n        string += this.dateRangeStart.format(this.format) + ' - ';\n      }\n\n      if (this.dateRangeEnd && this.dateRangeEnd.format) {\n        string += this.dateRangeEnd.format(this.format);\n      }\n\n      return string ? string : 'Choose Date';\n    },\n\n    /**\n     * Get next calendar month\n     *\n     * @return {number}\n     */\n    nextActiveMonth() {\n      return this.active.start.month >= 11 ? 0 : this.active.start.month + 1;\n    },\n\n    /**\n     * Get first weekday of month\n     * 0 sunday , 6 saturday\n     *\n     * @return {number}\n     */\n    startMonthDay() {\n      return new Date(this.active.start.year, this.active.start.month, 1).getDay();\n    },\n\n    /**\n     * Get last day of month\n     *\n     * @return {number}\n     */\n    endMonthDate() {\n      return new Date(this.active.end.year, this.nextActiveMonth, 0).getDate();\n    },\n\n\n    /**\n     * Get first weekday of next month\n     * 0 sunday , 6 saturday\n     *\n     * @return {number}\n     */\n    startNextMonthDay() {\n      return new Date(this.active.end.year, this.nextActiveMonth, 1).getDay();\n    },\n\n\n    /**\n     * Get last day of next month\n     *\n     * @return {number}\n     */\n    endNextMonthDate() {\n      return new Date(this.active.end.year, this.active.start.month + 2, 0).getDate();\n    },\n  },\n\n  methods: {\n\n    /**\n     * Get the position relative to month\n     *\n     * @param {number} wi Week number 1-6\n     * @param {number} di Day of week 1-7\n     * @param {number} startMonthDay\n     *\n     * @return {number}\n     */\n    getDayIndexInMonth(wi, di, startMonthDay) {\n      let date = (7 * (wi - 1)) + di;\n      return date - startMonthDay;\n    },\n\n    /**\n     * Get Month day\n     *\n     * @param {number} wi Week number 1-6\n     * @param {number} di Day of week 1-7\n     * @param {number} startMonthDay\n     * @param {number} endMonthDate\n\n     * @return {number}\n     */\n    getDayCell(wi, di, startMonthDay, endMonthDate) {\n      let result = this.getDayIndexInMonth(wi, di, startMonthDay);\n\n      return result > 0 && result <= endMonthDate ? result : ' ';\n    },\n\n    /**\n     * Go to next month\n     */\n    goPrevMonth() {\n      let prevMonth = new Date(this.active.start.year, this.active.start.month, 0);\n\n      this.active.start.month = prevMonth.getMonth();\n      this.active.start.year = prevMonth.getFullYear();\n      this.active.end.year = prevMonth.getFullYear();\n    },\n\n    /**\n     * Go to previous month\n     */\n    goNextMonth() {\n      let nextMonth = new Date(this.active.end.year, this.nextActiveMonth, 1);\n\n      this.active.start.month = nextMonth.getMonth();\n      this.active.start.year = nextMonth.getFullYear();\n      this.active.end.year = nextMonth.getFullYear();\n    },\n\n    /**\n     * Calc date from date params\n     *\n     * @return {dayjs}\n     */\n    calcDate(wi, di, next) {\n      let year = next ? this.active.end.year : this.active.start.year;\n      let month = next ? this.nextActiveMonth : this.active.start.month;\n      let startMonthDay = next ? 'startNextMonthDay' : 'startMonthDay';\n\n      let dayInMonth = this.getDayIndexInMonth(wi, di, this[startMonthDay]);\n\n\n      return dayInMonth > 0 ? dayjs(`${year}-${(month + 1)}-${dayInMonth}`) : false;\n    },\n\n    /**\n     * Check if day is in selectable\n     * @param {number} wi Week number 1-6\n     * @param {number} di Day of week 1-7\n     * @param {number} startMonthDay\n     * @param {number} endMonthDate\n     *\n     * @return {boolean}\n     */\n    isSelectable(wi, di, startMonthDay, endMonthDate) {\n      let result = this.getDayIndexInMonth(wi, di, startMonthDay);\n\n      return result > 0 && result <= endMonthDate;\n    },\n\n    /**\n     * Check if day is within selected range\n     * @param {number} wi Week number 1-6\n     * @param {number} di Day of week 1-7\n     * @param {boolean} next Use start of end\n     *\n     * @return {boolean}\n     */\n    isInRange(wi, di, next) {\n      let date = this.calcDate(wi, di, next);\n\n      if (!date) {\n        return false;\n      }\n\n      /** If start is null then cant be anything in range */\n      if (this.dateRangeStart === null) {\n        return false;\n      }\n\n      /** If is first choice then any date after is in range */\n      if (this.dateRangeEnd === null) {\n        return date.isSame(this.dateRangeStart);\n      }\n\n      /** Else assure in range */\n      return (date.isSame(this.dateRangeStart) || date.isAfter(this.dateRangeStart))\n          && (date.isSame(this.dateRangeEnd) || date.isBefore(this.dateRangeEnd));\n    },\n\n    /**\n     * Pick a date\n     *\n     * @param {number} wi Week number 1-6\n     * @param {number} di Day of week 1-7\n     * @param {boolean} next use start of end\n     */\n    select(wi, di, next) {\n\n      let date = this.calcDate(wi, di, next);\n\n      if (!date) {\n        return;\n      }\n\n      if (this.isFirstChoice) {\n        this.dateRangeStart = date;\n        this.dateRangeEnd = null;\n      } else {\n        this.dateRangeEnd = date;\n      }\n\n      this.isFirstChoice = !this.isFirstChoice;\n    },\n\n    /**\n     * Set a preset\n     *\n     * @param preset {{label: string, dateRange: {start: dayjs, end: dayjs}}}\n     */\n    setPreset(preset) {\n      this.setRange(preset.dateRange);\n      this.selectedPreset = preset.label;\n    },\n\n    /**\n     * Set date range\n     *\n     * @param dateRange {{start: dayjs, end: dayjs}}\n     */\n    setRange(dateRange) {\n      if (dateRange) {\n        /** If is not a dayjs, load into a dayjs */\n        if (\n            typeof dateRange.start !== 'object'\n            || typeof dateRange.start.$d === 'undefined'\n        ) {\n\n          /** If is a moment object extract js Date */\n          if (dateRange.start._isAMomentObject) {\n            dateRange.start = dateRange.start._d;\n          }\n\n          dateRange.start = dayjs(dateRange.start);\n        }\n\n        if (\n            typeof dateRange.end !== 'object'\n            || typeof dateRange.end.$d === 'undefined'\n        ) {\n\n          /** If is a moment object extract js Date */\n          if (dateRange.end._isAMomentObject) {\n            dateRange.end = dateRange.end._d;\n          }\n\n          dateRange